* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  color: #fff;
}

body {
  font-family: "montseratt", sans-serif;
}

.app {
  background-image: url("./img/purp-bg.jpg");
  background-size: cover;
  background-position: top;
  transition: 0.4 ease-out;
}

main {
  min-height: 100vh; /* 1vh = 1% of viewport*/
  padding: 25px;
}

.search-box {
  width: 100%;
  margin: 35px 0 35px;
}

.search-box .search-bar {
  display: block;
  width: 55%;
  padding: 12px;

  appearance: none;
  background: none;
  border: none;
  outline: none;

  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 25px 25px 25px 25px;

  margin: 0 auto;
}

.location-box {
  text-align: center;
}

.location-box .location {
  font-size: 28px;
  font-weight: bold;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}

.location-box .date {
  font-size: 18px;
  font-weight: lighter;
  text-shadow: 2px 2px rgba(50, 50, 70, 0.5);
}

.weather-box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  row-gap: 10px;
  padding: 40px;
}

.weather-box .weather {
  font-size: 25px;
  font-weight: bold;
  color: rgb(53, 37, 89);
}

.weather-box .temp {
  font-size: 50px;
  font-weight: lighter;
  text-shadow: 3px 6px rgba(50, 50, 70, 0.5);
}

.forecast-box {
  display: flex;
  flex-direction: row;
  justify-content: center;

  width: 100%;
  height: 110px;

  border-radius: 5px 5px 5px 5px;
  background-color: rgba(77, 91, 165, 0.5);
}

.forecast {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  padding: 5px;
  width: 80px;
  font-size: 10px;
}

.forecast > div > img {
  width: 30px;
  height: 30px;
}

.error {
  text-align: center;
  font-size: 40px;
  font-weight: bolder;
}

/* xsm devices */
@media only screen and (max-width: 576px) {
  .forecast-box {
    text-align: center;
  }
}

/* md devices and up */
@media only screen and (min-width: 768px) {
  .weather-box {
    padding: 20px;
  }

  .forecast-box {
    column-gap: 30px;
    background-color: transparent;
  }

  .forecast {
    width: 150px;
    height: 150px;
    border-radius: 5px 5px 5px 5px;
    background-color: rgba(77, 91, 165, 0.5);
    font-size: 14px;
  }

  .forecast > div > img {
    width: 70px;
    height: 70px;
  }
}

/* lg devices and up */
@media only screen and (min-width: 992px) {
  .search-box {
    width: 100%;
    margin: 0 0 70px;
  }
  .search-box .search-bar {
    width: 35%;
  }

  .weather-box {
    padding: 30px;
  }

  .forecast-box {
    column-gap: 60px;
    background-color: transparent;
  }

  .forecast {
    width: 150px;
    height: 200px;
    border-radius: 5px 5px 5px 5px;
    background-color: rgba(77, 91, 165, 0.5);
    font-size: 16px;
  }

  .forecast > div > img {
    width: 100px;
    height: 100px;
  }
}
